@import url(https://fonts.googleapis.com/css?family=Raleway|Work+Sans);
header {
  font-family: 'Work Sans', Arial, Helvetica, sans-serif;
  z-index: 0;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Raleway', Verdana, Geneva, Tahoma, sans-serif;
}

h2 {
  font-size: 1.5rem;
}

#scores {
  margin-top: 64px;
  padding: 1rem 0;
  position: fixed;
  width: 100%;
  height: 4rem;
}

.status, .score, .high-score {
  margin: 0;
}

#banner {
  position: relative;
  padding-top: calc(4rem + 64px);
}

#banner #instructions {
  position: absolute;
  top: 50%;
  width: 100%;
  z-index: -1;
}

#banner #instructions .col {
  background-color: rgba(255, 255, 255, 0.75);
  border-radius: 1rem;
}

#banner #instructions h2 {
  margin-top: 1rem;
  font-size: 2rem;
  font-weight: 700;
}

#banner #instructions ul {
  padding-left: 1rem;
}

#banner #instructions ul li{
  list-style-type: square;
}

@media screen and (max-width: 600px) {
  nav .brand-logo {
    width: 100%;
  }

  #scores {
    margin: 56px auto 0;
    padding: 1rem;
    height: 5rem;
  }
}
main.container {
  margin-top: 564px;
}
.card .card-image img {
  max-width: 400px;
  height: auto;
  margin: auto;
  padding: 1rem;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

